			//Chowthri K Loganathan : 10/02/2013 - Updated 04242017
			this.CalculateElectionPeriod = function (PartA, PartB, PlanName, PlanType, PlanYear, AppType, DOB, currentDate) {

						this.aepStart = this.configurationService.getAnualEnrollmentPeriodFrom();
						this.aepEnd = this.configurationService.getAnualEnrollmentPeriodTo();

						var atPartADate;
						var atPartBDate;
						var atPartABDate;
						var atisIEPPass;
						var atisICEPPass;
						var atCurrentdate;

						//Find CurrentYear, FutureYear
						var atCurrentYear;
						var atFutureYear;
						var atPrevYear;
						var atCurrentDateInput;
						var FutureYear;
						var EPLData = EPLTemplate();

						atCurrentDateInput = new Date();

						if (atCurrentDateInput.getFullYear() + 1 == PlanYear) {
									atCurrentYear = atCurrentDateInput.getFullYear();
									atFutureYear = atCurrentDateInput.getFullYear() + 1;
						} else if (atCurrentDateInput.getFullYear() + 1 != PlanYear && atCurrentDateInput.getYear - 1 != PlanYear) {
									FutureYear = atCurrentDateInput.getFullYear();
									atCurrentYear = atCurrentDateInput.getFullYear();
						}
						if (atFutureYear = atCurrentYear) {
									atPrevYear = atFutureYear - 1;
						} else {
									atPrevYear = atCurrentYear;
						}

						atPartADate = new Date(PartA);
						atPartBDate = new Date(PartB);

						//Consider Later date between the partA && PartB date if the Plan is MA ||MAPD
						if (PlanName == "PDP") {
									atPartABDate = GetPartAB(PartA, PartB, PlanName);
						} else if (PlanName == "MA" || PlanName == "MAPD") {
									atPartABDate = GetPartAB(PartA, PartB, PlanName);
						}

						//Election periods, Enable ||Disable for ICEP , IEP, OEPNew
						// Calculate Age in Window
						//START ICEP Determine whether the ICEP to be enabled ||not
						var atCurrentDate = new Date();
						var atPriorDate = new Date();
						var atAfterDate = new Date();
						var atPartBDatePriorCheck = new Date();

						atCurrentdate = atCurrentDateInput;

						atPriorDate = DateAdd("MM", -3, atPartABDate);
						atAfterDate = DateAdd("DD", -1, DateAdd("MM", 4, atPartABDate));

						atisICEPPass = "O";

						//For ICEP Delayed PArtB
						atPartBDatePriorCheck = DateAdd("MM", -3, atPartBDate);

						//if app type is AEF always ICEP should be disabled
						if (AppType == "IND") {

									//Added additional check for MA - logic by Kurt
									if (isDateEqual(atPartADate, atPartBDate) && PlanName == "MA") {

												if (atCurrentdate >= atPriorDate && atCurrentDate <= atAfterDate) {

															if (PlanYear > retyear(DateAdd("MM", 1, atCurrentDateInput))) {

																		if (atPartABDate > atCurrentdate) {

																					atisICEPPass = "Y";
																		} else {

																					atisICEPPass = "N";
																		}
															} else {
																		atisICEPPass = "Y";
															}

															EPLData = InsertEPL(EPLData, "Q1", PlanName, "ICEP", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, atisICEPPass);
												} else {
															atisICEPPass = "N";

															EPLData = InsertEPL(EPLData, "Q1", PlanName, "ICEP", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "N");
												}
									} else if (atPartBDate > atPartADate) {
												if (atPartBDatePriorCheck > DateAdd("MM", -3, atCurrentDateInput)) {
															if (PlanYear > retyear(DateAdd("MM", 1, atCurrentDateInput))) {
																		if (atPartABDate > atCurrentdate) {
																					atisICEPPass = "Y";
																		} else {
																					atisICEPPass = "N";
																		}
															} else {
																		atisICEPPass = "Y";
															}

															//Calculate EPL
															EPLData = InsertEPL(EPLData, "Q1", PlanName, "ICEP", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, atisICEPPass);
												} else {

															atisICEPPass = "N";
															EPLData = InsertEPL(EPLData, "Q1", PlanName, "ICEP", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "N");
												}
									} else {

												atisICEPPass = "N";
												EPLData = InsertEPL(EPLData, "Q1", PlanName, "ICEP", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "N");
									}
						} else {
									atisICEPPass = "N";

									//Calculate EPL
									EPLData = InsertEPL(EPLData, "Q1", PlanName, "ICEP", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "N");

									//// if Apptype is AEF tehn ICEP should be disabled
						}

						//	//print atisICEPPass
						//////////End ICEP
						////-START OEPNEW
						if (retmonth(atPartABDate) == 1 || retmonth(atPartABDate) == 2 || retmonth(atPartABDate) == 12) {
									//Calculate EPL
									EPLData = InsertEPL(EPLData, "Q1", PlanName, "OEPNEW", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "N");
						} else {

									if (atCurrentDate <= atAfterDate && atPartABDate <= atCurrentDate && DateDiff("MM", atPartABDate, atAfterDate) == 3 && PlanYear == retyear(atCurrentDateInput)) {
												//Calculate EPL
												EPLData = InsertEPL(EPLData, "Q1", PlanName, "OEPNEW", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "Y");
									} else {
												//Calculate EPL
												EPLData = InsertEPL(EPLData, "Q1", PlanName, "OEPNEW", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "N");
									}
						}
						////-END OEP NEW
						//	// For IEP :- Make the IEP enable always, becuase there is no DOB in AEF. in order to calculate IEP
						//	// DOB is also considered. in MAPA 6.1 added date of birth to AEF && calculate it for IEP
						//	// for MAPA 6.0 Enable IEP always
						//
						//	// if PartA && PartB is not equal && currentdate is greater than the  PartAB Date Take earlier date between the PartA && PartB
						//	//Find PartAB date - earlier date between PartA && PartB
						//	// We have to consider earlier date between PartA && PartB, so we are passing
						//	// pdp as a parameter to get earlier date. Note: IEP will be disabled for pdp //
						var atIEPPartABDate = new Date();
						atIEPPartABDate = GetPartAB(PartA, PartB, "PDP");

						//// find whether the member is 65 years old
						var atAge;
						var atDOB7Month = new Date();

						var atPriorDateDOB = new Date();
						var atAfterDateDOB = new Date();
						var atIEPYears;
						var atIEPPriorDate = new Date();
						var atIEPAfterDate = new Date();

						atIEPPriorDate = DateAdd("MM", -3, atIEPPartABDate);
						atIEPAfterDate = DateAdd("DD", -1, DateAdd("MM", 4, atIEPPartABDate));

						//// Check for the 7 retmonth window period for IEP
						if (atCurrentdate >= atIEPPriorDate && atCurrentDate <= atIEPAfterDate) {
									atisIEPPass = "Y";

									//Calculate EPL
									EPLData = InsertEPL(EPLData, "Q1", PlanName, "IEP", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "Y");
						} else {
									atisIEPPass = "N";

									EPLData = InsertEPL(EPLData, "Q1", PlanName, "IEP", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "N");
						}

						////Check for IEPD ,//IEPD check if the PartAB date is out of 7 retmonth window period, && Age is 65 then, DOB retmonth && current retyear is 7 retmonth window period then Enable IEPD
						if (PlanYear == retyear(atCurrentDateInput)) {
									atAge = retyear(atCurrentDateInput) - retyear(convert("DateTime", DOB)); // //   dbo.FN_MP_GetAge(DOB)
						} else if (PlanYear == retyear(atCurrentDateInput) + 1) {
									atAge = PlanYear - retyear(convert("DateTime", DOB));
						}

						//Logic By  Kurt Beck 04242017
						var atMedicarePriorToAge65 = "N";

						if (atisIEPPass == "N" && atisICEPPass == "N") {
									if (atAge == 65) {
												atIEPYears = retyear(atCurrentDateInput);

												atDOB7Month = convert("DateTime", str(retmonth(DOB)) + "/" + "01/" + str(PlanYear));

												atPriorDateDOB = DateAdd("MM", -3, atDOB7Month);
												atAfterDateDOB = DateAdd("DD", -1, DateAdd("MM", 4, atDOB7Month));

												if (atCurrentdate >= atPriorDateDOB && atCurrentDate <= atAfterDateDOB) {
															var atMaxId;

															if (PlanName == "MA" || PlanName == "MAPD") {
																		//Calculate EPL
																		EPLData = UpdateEPLData(EPLData, "MAPD,MA", "IEP", "OnOffFlag", "Y", "", "");

																		if (retyear(atCurrentdate) == retyear(atDOB7Month) && retmonth(atCurrentdate) < retmonth(atDOB7Month) || retyear(atCurrentdate) < retyear(atDOB7Month) && retmonth(atCurrentdate) > retmonth(atDOB7Month)) {

																					//Calculate EPL
																					EPLData = UpdateEPLData(EPLData, "MAPD,MA,PDP", "IEP", "DelayedIEPFlag", "Y", "", "");

																					//Logic By  Kurt Beck 04242017
																					atMedicarePriorToAge65 = "Y";
																		}
																		////////-End://////////////////////////////////////////////////////////////////////////////
																		EPLData = InsertEPL(EPLData, "Q1", PlanName, "IEPD", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "Y");
															} else if (PlanName == "PDP") {

																		//Calculate EPL
																		EPLData = UpdateEPLData(EPLData, "MAPD,MA,PDP", "IEP", "OnOffFlag", "Y", "", "");

																		if (retyear(atCurrentdate) == retyear(atDOB7Month) && retmonth(atCurrentdate) < retmonth(atDOB7Month) || retyear(atCurrentdate) < retyear(atDOB7Month) && retmonth(atCurrentdate) > retmonth(atDOB7Month)) {
																					//Calculate EPL
																					EPLData = UpdateEPLData(EPLData, "MAPD,MA,PDP", "IEP", "DelayedIEPFlag", "Y", "", "");

																					//Logic By  Kurt Beck 04242017
																					atMedicarePriorToAge65 = "Y";
																		}

																		//Calculate EPL
																		EPLData = InsertEPL(EPLData, "Q1", PlanName, "IEPD", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "Y");
															}
												}
									}
						}

						////AEP  && OEP
						EPLData = InsertEPL(EPLData, "Q1", PlanName, "OEPI", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "Y");

						EPLData = InsertEPL(EPLData, "Q1", PlanName, "AEP", PlanYear, GetAEPDateRange("Start", this.aepStart), GetAEPDateRange("End", this.aepEnd), 1, EnableDisableAEP(currentDate, this.aepStart, this.aepEnd));

						//Close
						////if the Plan Type is PDP then OEPI should be N
						//// For OEPI
						//Calculate EPL
						EPLData = InsertEPL(EPLData, "Q1", PlanName, "OEPI", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "Y");

						EPLData = UpdateEPLData(EPLData, "PDP", "OEPI,ICEP,OEPNEW,OEP", "OnOffFlag", "N", "", "");

						EPLData = UpdateEPLData(EPLData, "MAPD", "OEPI", "OnOffFlag", "Y", "", "");

						EPLData = UpdateEPLData(EPLData, "PDP", "IEP", "OnOffFlag", "N", "", "");

						//// if the application type is AEF && ICEP should be disabled
						if (AppType == "AEF") {

									EPLData = UpdateEPLData(EPLData, "PDP", "ICEP", "OnOffFlag", "N", "", "");
						}

						if (PlanYear == retyear(atCurrentDateInput)) {

									EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "", "OnOffFlag", "N", "", "");
						}

						//	//	65th Bday && IEPD when the Birthday is in current retyear but enrolling in future retyear.
						//	//  DOB 11/15/1945 - makes me 65 this retyear
						//	//  IEP/IEPD runs 7 months
						//	//  Aug, Sept, Oct, NOV, Dec, Jan, Feb
						var atdob65 = new Date();

						atdob65 = DateAdd("yyyy", 65, DOB);

						////	select atdob65,atCurrentYear
						if (retyear(atdob65) == atCurrentYear) {
									atDOB7Month = convert("DateTime", str(retmonth(DOB)) + "/" + "01/" + str(retyear(atdob65)));
									atPriorDateDOB = DateAdd("MM", -3, atDOB7Month);
									atAfterDateDOB = DateAdd("DD", -1, DateAdd("MM", 4, atDOB7Month));

									if (atCurrentdate >= atPriorDateDOB && atCurrentDate <= atAfterDateDOB) {
												//Calculate EPL
												EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEP,IEPD", "OnOffFlag", "Y", "", "");

												EPLData = InsertEPL(EPLData, "Q1", PlanName, "IEPD", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "Y");
									}
						}

						if (retmonth(atdob65) == 11 && retmonth(atCurrentDateInput) == 11 && retmonth(atPartABDate) == 11 && PlanYear == retyear(atCurrentDateInput) + 1) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEP,IEPD,ICEP", "OnOffFlag", "N", "", "");
						}

						var atDteRange1 = new Date();
						var atDteRange2 = new Date();

						var atDteRange3 = new Date();
						var atDteRange4 = new Date();

						var atDteRange5 = new Date();
						var atDteRange6 = new Date();
						var atDteRange1101 = new Date();
						var atDteRange7 = new Date();
						var atDteRange8 = new Date();
						var atDteRangeChkDec;

						atDteRange1 = convert("DateTime", "01" + "/" + "01" + "/" + retyear(atCurrentDateInput));
						atDteRange2 = convert("DateTime", "11" + "/" + "30" + "/" + retyear(atCurrentDateInput));
						atDteRange1101 = convert("DateTime", "11" + "/" + "01" + "/" + retyear(atCurrentDateInput));

						atDteRange3 = convert("DateTime", "11" + "/" + "15" + "/" + retyear(atCurrentDateInput));
						atDteRange4 = convert("DateTime", "11" + "/" + "30" + "/" + retyear(atCurrentDateInput));

						atDteRange5 = convert("DateTime", "12" + "/" + "01" + "/" + retyear(atCurrentDateInput));
						atDteRange6 = convert("DateTime", "12" + "/" + "31" + "/" + retyear(atCurrentDateInput));

						atDteRange7 = convert("DateTime", "10" + "/" + "01" + "/" + retyear(atCurrentDateInput));
						atDteRange8 = convert("DateTime", "10" + "/" + "31" + "/" + retyear(atCurrentDateInput));

						var atCurDateCheck = new Date();
						atCurDateCheck = convert("DateTime", convert("DateTime", atCurrentDateInput));

						if (atCurDateCheck >= atDteRange1 && atCurDateCheck <= atDteRange2 && atCurrentdate >= atPriorDate && convert("DateTime", atCurrentDate) <= convert("DateTime", atAfterDate) && PlanYear == atCurrentYear && isDateEqual(atPartADate, atPartBDate) == true) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEP,ICEP", "OnOffFlag", "Y", "", "");
						}

						if (atCurDateCheck >= atDteRange3 && atCurDateCheck <= atDteRange4 && atCurrentdate >= atPriorDate && convert("DateTime", atCurrentDate) <= convert("DateTime", atAfterDate) && PlanYear == atFutureYear) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEP,ICEP", "OnOffFlag", "N", "", "");
						}

						if (atCurDateCheck >= atDteRange5 && atCurDateCheck <= atDteRange6 && atCurrentdate >= atPriorDate && convert("DateTime", atCurrentDate) <= convert("DateTime", atAfterDate) && PlanYear == atFutureYear) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEP,ICEP", "OnOffFlag", "Y", "", "");
						}

						var atMOF = new Date();
						var atEffectiveDate = new Date();
						var attempSTr = new Date();
						var atOneOneFutureDt = new Date();

						if (atCurDateCheck >= atDteRange3 && atCurDateCheck <= atDteRange4 && PlanYear == atFutureYear && atCurrentYear < atFutureYear) {

									atEffectiveDate = convert("DateTime", "01" + "/" + "01" + "/" + convert("varchar(4)", atFutureYear));
									atOneOneFutureDt = atEffectiveDate;
						} else {
									atEffectiveDate = DateAdd("MM", 1, atCurDateCheck);
									atOneOneFutureDt = convert("DateTime", "01" + "/" + "01" + "/" + convert("varchar(4)", atFutureYear));
						}

						atMOF = atPartABDate;

						//// Check 7 retmonth window period
						var atifYes7;
						var atifYes3;

						atifYes7 = 0;
						atifYes3 = 0;

						//// Check for the seven retmonth window period
						if (atCurrentdate >= atPriorDate && convert("DateTime", atCurrentDate) <= convert("DateTime", atAfterDate)) {
									atifYes7 = 1;
						}

						////check for 3 months prior
						if (atPartADate != PartB && atifYes7 == 0) {
									if (atPartBDatePriorCheck > DateAdd("MM", -3, atCurrentDateInput)) {
												atifYes3 = 1;
									}
						}

						var atdteRange;

						if (atCurrentdate >= atDteRange5 && atCurrentdate <= atDteRange6) {

									atdteRange = 1;
						} else {
									atdteRange = 2;
						}

						if (atdteRange == 2 && PlanYear == atFutureYear && retyear(atCurrentDate) == atCurrentYear) {
									if (atPartABDate < atOneOneFutureDt || atPartABDate != atOneOneFutureDt) {

												EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEP,ICEP,IEPD", "OnOffFlag", "N", "", "");
									}
						}

						if (atdteRange == 1 && PlanYear == atFutureYear) {

									if (atCurrentdate >= atPriorDate && convert("DateTime", atCurrentDate) <= convert("DateTime", atAfterDate)) {
												//Calculate EPL
												EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "ICEP", "OnOffFlag", "Y", "", "");
									}
						}

						if (atifYes7 == 1 || atifYes3 == 1) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEPD", "OnOffFlag", "N", "", "");
						}

						if (atifYes7 != 1 && atifYes3 != 1 && retyear(atdob65) == atCurrentYear) {

									if (retyear(atdob65) == atCurrentYear) {
												atDOB7Month = convert("DateTime", str(retmonth(DOB)) + "/" + "01/" + str(retyear(atdob65)));
												atPriorDateDOB = DateAdd("MM", -3, atDOB7Month);
												atAfterDateDOB = DateAdd("DD", -1, DateAdd("MM", 4, atDOB7Month));

												if (atCurrentdate >= atPriorDateDOB && atCurrentDate <= atAfterDateDOB) {

															//Calculate EPL
															EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEPD,IEP", "OnOffFlag", "Y", "", "");

															EPLData = InsertEPL(EPLData, "Q1", PlanName, "IEPD", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "Y");
												}
									}
						}

						if (atifYes7 != 1 && atifYes3 != 1 && retyear(atdob65) < atCurrentYear) {

									if (retyear(atdob65) < atCurrentYear) {
												atDOB7Month = convert("DateTime", str(retmonth(DOB)) + "/" + "01/" + str(retyear(atdob65)));
												atPriorDateDOB = DateAdd("MM", -3, atDOB7Month);
												atAfterDateDOB = DateAdd("DD", -1, DateAdd("MM", 4, atDOB7Month));

												if (atCurrentdate >= atPriorDateDOB && atCurrentDate <= atAfterDateDOB) {

															//Calculate EPL
															EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEPD,IEP", "OnOffFlag", "Y", "", "");

															EPLData = InsertEPL(EPLData, "Q1", PlanName, "IEPD", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "Y");
												}
									}
						}

						if (atifYes7 != 1 && atifYes3 != 1 && retyear(atdob65) == retyear(atCurrentdate) + 1 && retyear(atCurrentdate) == atCurrentYear) {

									atDOB7Month = convert("DateTime", str(retmonth(DOB)) + "/" + "01/" + str(retyear(atdob65)));
									atPriorDateDOB = DateAdd("MM", -3, atDOB7Month);
									atAfterDateDOB = DateAdd("DD", -1, DateAdd("MM", 4, atDOB7Month));

									if (atCurrentdate >= atPriorDateDOB && atCurrentDate <= atAfterDateDOB) {
												//Calculate EPL
												EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEPD,IEP", "OnOffFlag", "Y", "", "");

												EPLData = InsertEPL(EPLData, "Q1", PlanName, "IEPD", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "Y");
									}
						}

						////select atPartABDate,atDteRange3,atDteRange4,retyear(atdob65),atdob65,atCurrentYear
						if (atPartABDate == atDteRange1101 && PlanYear == atCurrentYear && retyear(atdob65) == atCurrentYear && atCurrentDate >= atDteRange1 && atCurrentDate <= atDteRange4) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "ICEP,IEP", "OnOffFlag", "Y", "", "");
						}

						if (isDateEqual(atPartABDate, atDteRange5) == true && PlanYear == atCurrentYear && retyear(atdob65) == atCurrentYear && atCurrentDate >= atDteRange1 && atCurrentDate <= atDteRange4) {

									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "ICEP,IEP", "OnOffFlag", "Y", "", "");
						}

						if (atPartBDatePriorCheck > DateAdd("MM", -3, atCurrentDateInput) && (PlanType == "MAPD" || PlanName == "MA") && atPartADate < atPartBDate && PlanYear == atCurrentYear && retyear(atPartABDate) == atFutureYear) {

									if (atPartABDate > atCurrentdate) {
												//Calculate EPL
												EPLData = UpdateEPLData(EPLData, "MAPD,MA", "ICEP", "OnOffFlag", "Y", "", "");
									}
						}

						if (atPartBDatePriorCheck > DateAdd("MM", -3, atCurrentDateInput) && (PlanType == "MAPD" || PlanName == "MA") && atPartADate < atPartBDate && retyear(atPartABDate) == atFutureYear && PlanYear == atFutureYear) {

									if (atPartABDate > atCurrentdate) {
												//Calculate EPL
												EPLData = UpdateEPLData(EPLData, "MAPD,MA", "ICEP", "OnOffFlag", "Y", "", "");
									}
						}

						if (retmonth(atPartABDate) == 12 && PlanYear == atCurrentYear && atifYes7 == 1 && atCurrentDate >= atDteRange3 && atCurrentDate <= atDteRange4) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,MA", "ICEP", "OnOffFlag", "Y", "", "");
						}

						if (retmonth(atPartABDate) == 11 && PlanYear == atCurrentYear && atifYes7 == 1 && atCurrentDate >= atDteRange7 && atCurrentDate <= atDteRange8) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,MA", "ICEP", "OnOffFlag", "Y", "", "");
						}

						if (retmonth(atPartABDate) == 10 && PlanYear == atCurrentYear && atifYes7 == 1 && atCurrentDate >= atDteRange7 && atCurrentDate <= atDteRange8 && isDateEqual(atPartADate, atPartBDate) == true) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,MA", "ICEP", "OnOffFlag", "Y", "", "");
						}

						if (retmonth(atPartABDate) == 12 && PlanYear == atCurrentYear && atifYes7 == 1 && atCurrentDate >= atDteRange7 && atCurrentDate <= atDteRange8) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,MA", "ICEP", "OnOffFlag", "Y", "", "");
						}

						if (atifYes7 == 1 && PlanYear == atCurrentYear) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,MA", "ICEP", "OnOffFlag", "Y", "", "");
						}

						if (atCurrentdate >= atIEPPriorDate && convert("DateTime", atCurrentDate) <= convert("DateTime", atIEPAfterDate)) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MAPD,MA,PDP", "ICEP,IEP", "OnOffFlag", "Y", "", "");
						}

						if (atifYes7 = 1 && atCurrentDate >= atDteRange3 && atCurrentDate <= atDteRange4) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "PDP", "IEP", "OnOffFlag", "Y", "", "");
						}

						atDteRangeChkDec = 0;
						if (atCurrentDate >= atDteRange5 && atCurrentDate <= atDteRange6) {
									atDteRangeChkDec = 1;
						}

						if (PlanYear == atFutureYear && retyear(atCurrentDate) == atCurrentYear && atCurrentDate >= atDteRange1101 && atCurrentDate <= atDteRange4) {

									if (atifYes7 == 1 && atDteRangeChkDec == 0 && PlanYear == atFutureYear && retyear(atPartABDate) != atFutureYear) {
												//DateTable
												EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEP,ICEP", "OnOffFlag", "N", "", "");
									}
						}

						if (atPartADate < atPartBDate && atisICEPPass == "N") {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "PDP,MAPD", "ICEP", "OnOffFlag", "N", "", "");
						}

						//Calculate EPL
						EPLData = UpdateEPLData(EPLData, "PDP", "ICEP", "OnOffFlag", "N", "", "");

						if (AppType == "AEF") {
									if (retyear(atdob65) == atCurrentYear) {
												if (retyear(atdob65) == atCurrentYear) {
															atDOB7Month = convert("DateTime", str(retmonth(DOB)) + "/" + "01/" + str(retyear(atdob65)));
															atPriorDateDOB = DateAdd("MM", -3, atDOB7Month);
															atAfterDateDOB = DateAdd("DD", -1, DateAdd("MM", 4, atDOB7Month));

															if (atCurrentdate >= atPriorDateDOB && atCurrentDate <= atAfterDateDOB) {

																		//Calculate EPL
																		EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEP,IEPD", "OnOffFlag", "Y", "", "");

																		EPLData = InsertEPL(EPLData, "Q1", PlanName, "IEPD", PlanYear, convert("DateTime", "01/01/1800"), convert("DateTime", "01/01/1800"), 1, "Y");
															} else {
																		//Calculate EPL
																		EPLData = UpdateEPLData(EPLData, "MA,PDP,MAPD", "IEP,IEPD", "OnOffFlag", "N", "", "");
															}
												}
									} else if (retyear(atdob65) == atFutureYear) {
												atDOB7Month = convert("DateTime", str(retmonth(DOB)) + "/" + "01/" + str(retyear(atdob65)));
												atPriorDateDOB = DateAdd("MM", -3, atDOB7Month);
												atAfterDateDOB = DateAdd("DD", -1, DateAdd("MM", 4, atDOB7Month));

												if (atCurrentdate >= atPriorDateDOB && atCurrentDate <= atAfterDateDOB) {
															//Calculate EPL;
															EPLData = UpdateEPLData(EPLData, "PDP,MAPD", "IEP,IEPD", "OnOffFlag", "Y", "", "");
												} else {
															//Calculate EPL
															EPLData = UpdateEPLData(EPLData, "PDP,MAPD,MA", "IEP,IEPD", "OnOffFlag", "N", "", "");
												}
									} else {
												//Calculate EPL
												EPLData = UpdateEPLData(EPLData, "PDP,MAPD,MA", "IEP,IEPD", "OnOffFlag", "N", "", "");
									}

									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "PDP,MAPD,MA", "ICEP", "OnOffFlag", "N", "", "");
						}

						//// Get the DelayedFlag
						var atDelayFlg;

						//Calculate EPL
						atDelayFlg = GetDelayedFlagByEnrollmentType(EPLData, "IEP");

						if (atDelayFlg == "Y") {
									var atPerson65On = new Date();
									var atPriorDateCheck = new Date();
									var atAfterDateCheck = new Date();
									var atEffDate = new Date();

									atCurrentDate = GetDate();
									atEffDate = convert("DateTime", DateAdd("MM", 1, atCurrentDate));

									atEffDate = retmonth(atEffDate) + "/01/" + retyear(atEffDate);

									atEffDate = convert("DateTime", atEffDate);

									atPerson65On = DateAdd("yyyy", 65, DOB);

									atPriorDateCheck = DateAdd("MM", -3, atPerson65On);

									atPriorDateCheck = convert("DateTime", retmonth(atPriorDateCheck) + "/01/" + retyear(atPriorDateCheck));

									atAfterDateCheck = DateAdd("DD", -1, DateAdd("MM", 4, atPerson65On));

									if (atCurrentdate >= atPriorDateCheck && atCurrentDate <= atAfterDateCheck && atPerson65On > atCurrentDate) {
												//Calculate EPL
												EPLData = UpdateEPLData(EPLData, "PDP,MAPD,MA", "IEP", "DelayedIEPFlag", "Y", "", "");
									}

									if (atCurrentdate >= atPriorDateCheck && atCurrentDate <= atAfterDateCheck && atPerson65On > atCurrentDate && retmonth(atPerson65On) != retmonth(atCurrentDate)) {

												atEffDate = ltrim(str(retmonth(atPerson65On))) + "/01/" + ltrim(rtrim(str(retyear(atPerson65On))));
												atEffDate = convert("DateTime", atEffDate);

												//Calculate EPL
												EPLData = UpdateEffectiveDate(EPLData, "IEP", "Y", atEffDate);
									}
						}

						//Calculate EPL
						EPLData = UpdateEPLData(EPLData, "MA", "IEP", "OnOffFlag", "N", "", "");

						var atisICEPYes; // varchar(1)
						var atisIEPYes; // varchar(1)
						//Calculate EPL
						atisICEPYes = GetOnOffFlagByEnrollmentType(EPLData, "ICEP");
						atisIEPYes = GetOnOffFlagByEnrollmentType(EPLData, "IEP");

						if (atisICEPYes == "Y" || atisIEPYes == "Y") {
									if (PlanYear != retyear(GetDate()) && PlanYear == atFutureYear && atCurrentDate < atDteRange5 && retyear(atPartABDate) == retyear(atCurrentDate)) {
												//Calculate EPL
												EPLData = UpdateEPLData(EPLData, "MA,MAPD", "IEP,ICEP", "OnOffFlag", "N", "", "");
									}
						}

						//// Disable AEP for Particular DateRange
						var aticepStartDate = new Date();
						var aticepEndDate = new Date();

						// //PageDisplay
						// aticepStartDate = dates.PlanDateStart;
						// aticepEndDate = dates.PlanDateEnd;

						// if (retyear(atPartABDate) > atCurrentYear && retmonth(atPartABDate) > 1 && (GetDate() >= aticepStartDate && GetDate() <= aticepEndDate)) {
						//     //Calculate EPL
						//     EPLData = UpdateEPLData(EPLData, "MA,MAPD,PDP", "AEP", "OnOffFlag", "N", "", "");

						// }

						if (retyear(atPartABDate) > atCurrentYear && retmonth(atPartABDate) > 1) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MA,MAPD,PDP", "AEP", "OnOffFlag", "N", "", "");
						}

						if (atifYes7 == 1) {
									//Calculate EPL
									EPLData = UpdateEPLData(EPLData, "MA,MAPD,PDP", "IEP", "EffDate", "NULL", "", "");
						}

						var atICEPIEPStartDate = GetICEPIEPRange("ICEPIEPRange", "Start");
						var atICEPIEPEndDate = GetICEPIEPRange("ICEPIEPRange", "End");
						//aticepEndDate = dates.PlanDateEnd;

						//alert("Current Date " + GetDate());
						//alert("ICEP -" + atICEPIEPEndDate);
						//alert("PartAB " + atPartABDate);

						if (retyear(atPartABDate) > atCurrentYear && GetDate() >= atICEPIEPStartDate && GetDate() <= atICEPIEPEndDate) {
									//Calculate EPL

									//alert("Inside Logic");

									EPLData = UpdateEPLData(EPLData, "MA,MAPD,PDP", "ICEP", "OnOffFlag", "Y", "", "");
									EPLData = UpdateEPLData(EPLData, "MA,MAPD,PDP", "IEP", "OnOffFlag", "Y", "", "");
						}

						//Logic By Dezetta : on Oct 29 2012
						//Logic change for IEP: if the PlanYear is in Future and PlanType is PDP and month is lesserthan the DEC
						//then Disable IEP

						if (atCurrentYear != retyear(GetDate()) && (PlanName == "MAPD" || PlanName == "PDP") && retmonth(GetDate()) < 12 && retyear(atPartABDate) != atFutureYear) {

									EPLData = UpdateEPLData(EPLData, "MAPD,PDP", "IEP,IEPD", "OnOffFlag", "N", "", "");
						}

						//Logic By Kurt Beck 04242017

						//user should not be able to see ICEP/IEP for a delayed Part B when the current date is later than the Part B date
						//EPL Story--B222128

						if (isDateEqual(atPartADate, atPartBDate)) {
									if (atPartBDatePriorCheck < DateAdd("MM", -3, atCurrentDateInput)) {
												if (PlanYear > retyear(atCurrentDateInput)) {
															if (atPartABDate < atCurrentDateInput && retmonth(GetDate()) == 12) {
																		EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEP,ICEP", "OnOffFlag", "N", "", "");
															}
												}
									}
						} else {
									if (atPartBDatePriorCheck > DateAdd("MM", -3, atCurrentDateInput)) {
												var atdICEP = "";
												var atdIEP = "";
												var atdIEPD = "";

												atdICEP = GetOnOffFlagByEnrollmentType(EPLData, "ICEP");
												atdIEP = GetOnOffFlagByEnrollmentType(EPLData, "IEP");
												atdIEPD = GetOnOffFlagForDelayedIEPFlag(EPLData, "IEP");
									}
						}

						//for 65th Birthday
						if (atMedicarePriorToAge65 == "Y") {
									EPLData = UpdateEPLData(EPLData, "MAPD,PDP,MA", "IEP", "OnOffFlag", "Y", "", "");
						}

						if (isDateEqual(atPartADate, atPartBDate) && (PlanName == "MAPD" || PlanName == "PDP")) {
									EPLData = UpdateEPLData(EPLData, "MAPD,PDP", "ICEP", "OnOffFlag", "N", "", "");
						}

						if (!isDateEqual(atPartADate, atPartBDate) && (PlanName == "MAPD" || PlanName == "PDP")) {
									EPLData = UpdateEPLData(EPLData, "MAPD,PDP", "IEP", "OnOffFlag", "N", "", "");
						}

						/////


						return EPLData;
			};

			//Logic By Kurt Beck 04242017
			function GetOnOffFlagForDelayedIEPFlag(data, EnrollmentType) {

						var onOff;

						for (var k = 0; k <= data.length - 1; k++) {
									if (data[k].EnrollmentType == EnrollmentType) {
												onOff = data[k].DelayedIEPFlag;
									}
						}
						return onOff;
			}

			function isDateEqual(date1, date2) {
						return date1.valueOf() == date2.valueOf();
			}

			function PageDisplay(Option, dtOption) {
						var dtDate;

						if (Option == "ICEPIEPRange" && dtOption == "Start") {
									dtDate = new Date("2013-10-01 00:00:00.000");
						} else if (Option == "ICEPIEPRange" && dtOption == "End") {
									dtDate = new Date("2013-11-30 23:59:00.000");
						}

						return dtDate;
			}

			function GetICEPIEPRange(Option, dtOption) {
						var dtDate;

						if (Option == "ICEPIEPRange" && dtOption == "Start") {
									dtDate = new Date("2013-10-01 00:00:00.000");
						} else if (Option == "ICEPIEPRange" && dtOption == "End") {
									dtDate = new Date("2013-11-30 23:59:00.000");
						}
						return dtDate;
			}

			//modified correct AEP date range But needs to come from sync
			function GetAEPDateRange(option, date) {
						var dte;
						var year = new Date().getFullYear();

						if (option == "Start") {
									dte = new Date(year + "-" + date + " 00:00:00.000");
						} else if (option == "End") {
									dte = new Date(year + "-" + date + " 23:59:00.000");
						}
						return dte;
			}

			//Modified correct AEP date range. But needs to come from sync
			function EnableDisableAEP(currentDate, startDateVal, endDateVal) {
						var isOnOff = "N";
						var year = currentDate.getFullYear();
						var startDate = new Date(year + "-" + startDateVal + " 00:00:00.000");
						var endDate = new Date(year + "-" + endDateVal + " 23:59:00.000");

						if (currentDate >= startDate && currentDate <= endDate) {
									isOnOff = "Y";
						}
						return isOnOff;
			}

			function InsertEPL(data, ChannelCode, ProductType, EnrollmentType, PlanYear, StartDate, EndDate, NoOfMonths, OnOffFlag, DelayedIEPFlag, EffDate) {

						data.push({ "ChannelCode": ChannelCode, "ProductType": ProductType, "EnrollmentType": EnrollmentType, "PlanYear": PlanYear, "StartDate": StartDate,
									"EndDate": EndDate, "NoOfMonths": NoOfMonths, "OnOffFlag": OnOffFlag, "DelayedIEPFlag": DelayedIEPFlag, "EffDate": EffDate
						});
						return data;
			}

			function EPLTemplate() {
						var data = [];
						data.push({ "ChannelCode": '', "ProductType": '', "EnrollmentType": '', "PlanYear": '', "StartDate": '',
									"EndDate": '', "NoOfMonths": 0, "OnOffFlag": '', "DelayedIEPFlag": '', "EffDate": ''
						});

						return data;
			}

			function UpdateEPL(data, ChannelCode, ProductType, EnrollmentType, PlanYear, StartDate, EndDate, NoOfMonths, OnOffFlag, DelayedIEPFlag, EffDate) {

						data.push({ "ChannelCode": ChannelCode, "ProductType": ProductType, "EnrollmentType": EnrollmentType, "PlanYear": PlanYear, "StartDate": StartDate,
									"EndDate": EndDate, "NoOfMonths": NoOfMonths, "OnOffFlag": OnOffFlag, "DelayedIEPFlag": DelayedIEPFlag, "EffDate": EffDate
						});
						return data;
			}

			function GetDelayedFlagByEnrollmentType(data, EnrollmentType) {

						var delayedFlag;

						for (var k = 0; k <= data.length - 1; k++) {
									if (data[k].EnrollmentType == EnrollmentType) {
												delayedFlag = data[k].DelayedIEPFlag;
									}
						}
						return delayedFlag;
			}

			function GetOnOffFlagByEnrollmentType(data, EnrollmentType) {

						var onOff;

						for (var k = 0; k <= data.length - 1; k++) {
									if (data[k].EnrollmentType == EnrollmentType) {
												onOff = data[k].OnOffFlag;
									}
						}
						return onOff;
			}

			this.UpdateEffectiveDate = function (data, EnrollmentType, isDelayedFlag, EffectiveDate) {

						for (var j = 0; j <= data.length - 1; j++) {

									if (data[j].EnrollmentType == EnrollmentType && data[j].DelayedIEPFlag == isDelayedFlag) {
												data[j].EffDate = EffectiveDate;
									}
						}
						return data;
			};

			function UpdateEPLData(data, LookupField1, LookupField2, UpdateField1, UpdateValue1, UpdateField2, UpdateValue2) {

						var splitsLookField1 = LookupField1.split(",");
						var splitsLookField2 = LookupField2.split(",");

						for (var i = 0; i <= splitsLookField1.length - 1; i++) {

									var whereField1 = splitsLookField1[i].toString();

									for (var j = 0; j <= splitsLookField2.length - 1; j++) {

												var whereField2 = splitsLookField2[j].toString();

												for (var k = 0; k <= data.length - 1; k++) {

															if (data[k].ProductType == whereField1 && data[k].EnrollmentType == whereField2) {

																		if (UpdateValue1 != "") {
																					if (UpdateField1 == "OnOffFlag") {
																								data[k].OnOffFlag = UpdateValue1;
																					} else if (UpdateField1 == "DelayedIEPFlag") {
																								data[k].DelayedIEPFlag = UpdateValue1;
																					} else if (UpdateField1 == "EffDate") {
																								data[k].EffDate = UpdateValue1;
																					} else if (UpdateField1 == "EnrollmentType") {
																								data[k].EnrollmentType = UpdateValue1;
																					} else if (UpdateField1 == "PlanYear") {
																								data[k].PlanYear = UpdateValue1;
																					} else if (UpdateField1 == "StartDate") {
																								data[k].StartDate = UpdateValue1;
																					} else if (UpdateField1 == "EndDate") {
																								data[k].EndDate = UpdateValue1;
																					} else if (UpdateField1 == "NoOfMonths") {
																								data[k].NoOfMonths = UpdateValue1;
																					}
																		}
																		if (UpdateValue2 != "") {
																					if (UpdateField2 == "OnOffFlag") {
																								data[k].OnOffFlag = UpdateValue2;
																					} else if (UpdateField2 == "DelayedIEPFlag") {
																								data[k].DelayedIEPFlag = UpdateValue2;
																					} else if (UpdateField2 == "EffDate") {
																								data[k].EffDate = UpdateValue2;
																					} else if (UpdateField2 == "EnrollmentType") {
																								data[k].EnrollmentType = UpdateValue2;
																					} else if (UpdateField2 == "PlanYear") {
																								data[k].PlanYear = UpdateValue2;
																					} else if (UpdateField2 == "StartDate") {
																								data[k].StartDate = UpdateValue2;
																					} else if (UpdateField2 == "EndDate") {
																								data[k].EndDate = UpdateValue2;
																					} else if (UpdateField2 == "NoOfMonths") {
																								data[k].NoOfMonths = UpdateValue2;
																					}
																		}
															}
												}
									}
						}

						return data;
			}

			function ltrim(strValue) {
						for (var k = 0; k < strValue.length && isWhitespace(strValue.charAt(k)); k++) {}
						return strValue.substring(k, str.length);
			}

			function rtrim(strValue) {
						for (var j = strValue.length - 1; j >= 0 && isWhitespace(strValue.charAt(j)); j--) {}
						return strValue.substring(0, j + 1);
			}

			function trim(strValue) {
						strValue = strValue.replace(/^\s+/, '');
						strValue = strValue.replace(/\s+$/, '');
						return strValue;
			}

			function isWhitespace(charToCheck) {
						var whitespaceChars = " \t\n\r\f";
						return whitespaceChars.indexOf(charToCheck) != -1;
			}

			function GetDate() {
						var cTemp = new Date();
						return cTemp;
			}

			function convert(convertTo, objValue) {

						var cTemp = new Date();

						if (convertTo == "DateTime") {
									cTemp = new Date(objValue);
						} else {
									cTemp = objValue;
						}
						return cTemp;
			}

			function str(cValue) {
						return cValue.toString();
			}

			function retmonth(dtDate) {
						var tempDte = new Date(dtDate);
						return tempDte.getMonth() + 1;
			}

			function DateAdd(sInterval, iNum, atDate) {
						var dTemp = new Date(atDate);
						if (!sInterval || iNum == 0) return dTemp;

						if (sInterval == "MM") {
									dTemp.setMonth(dTemp.getMonth() + iNum);
						} else if (sInterval == "DD") {
									dTemp.setDate(dTemp.getDate() + iNum);
						} else if (sInterval == "yyyy") {
									dTemp.setFullYear(dTemp.getFullYear() + iNum);
						}

						return dTemp;
			}

			function retyear(dtDate) {
						var tempDte = new Date(dtDate);
						return tempDte.getFullYear();
			}

			function GetPartAB(PartA, PartB, PlanName) {

						var atPartADate = new Date(PartA);
						var atPartBDate = new Date(PartB);
						var atPartABDate = new Date();

						// Find Common date Between the PartA && PartB based on the Plan
						// if Plan name is PDP then Consider the Earlier Date between the PartA && PartB
						if (PlanName == "PDP") {

									if (atPartADate < atPartBDate) {
												atPartABDate = atPartADate;
									} else if (atPartBDate < atPartADate) {
												atPartABDate = atPartBDate;
									} else if (isDateEqual(atPartADate, atPartBDate)) {
												atPartABDate = atPartADate;
									}

									//if PartA ||PartB only selected then take either PartA ||PartB
									//In PDP always the earlier date should be selected if PartA && PartB both date are available
									// Whichever the date is not available that date will be 01/02/1900

									if (atPartADate <= new Date("01/02/1900")) {
												atPartABDate = atPartBDate;
									} else if (atPartBDate <= new Date("01/02/1900")) {
												atPartABDate = atPartADate.toDateString();
									}
						}
						//Consider Later date between the partA && PartB date if the Plan is MA ||MAPD
						else if (PlanName == "MA" || PlanName == "MAPD" || PlanName == "MAPD/MA") {
												if (atPartADate > atPartBDate) {
															atPartABDate = atPartADate;
												} else if (atPartBDate > atPartADate) {
															atPartABDate = atPartBDate;
												} else if (isDateEqual(atPartADate, atPartBDate)) {
															atPartABDate = atPartADate;
												}
									}

						return atPartABDate.toString("dd/MM/yyyy");
			}

			this.GetEffectiveDate = function (planYear) {
						var effDate = "";
						var currentDate = new Date();
						var year = currentDate.getFullYear();

						if (planYear == year + 1) {
									effDate = new Date(planYear + '/01/01');
						} else {
									effDate = DateAdd("MM", 1, currentDate);
						}

						return new Date(effDate.setDate(1));
			};

			function FormatDate(dteDate) {

						var d = dteDate;
						var temp = ("00" + (d.getMonth() + 1)).slice(-2) + "/" + ("00" + "01").slice(-2) + "/" + d.getFullYear();

						return temp;
			}

			function FormatDateWithDelimiter(dteDate, delimiter) {

						var d = dteDate;
						var temp = ("00" + (d.getMonth() + 1)).slice(-2) + delimiter + ("00" + d.getDate()).slice(-2) + delimiter + d.getFullYear();

						return temp;
			}

			function retmonth(dtDate) {
						var tempDte = new Date(dtDate);
						return tempDte.getMonth() + 1;
			}

